.game {
  background-image: url('../../Assets/Background/background2.jpg');
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  padding: 5%;
  padding-top: 0%;
  height: 100vh;
}

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* grid-column-gap: 20%; */
  grid-row-gap: 0px;
  justify-items: center;
  align-items: center;
}

.logoBlanco {
  width: 220px;
  /* margin-top: 20px; */
  align-self: center;
  justify-self: center;
}

.regresar {
  background-color: #ea5b27;
  -moz-border-radius: 28px;
  -webkit-border-radius: 28px;
  border-radius: 28px;
  border: 1px solid white;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  padding: 14px 41px;
  text-decoration: none;
  font-size: 25px;
  width: 50%;
  height: 60%;
  margin-top: 20px;
  grid-column-end: span 3;
  justify-self: end;
  margin: 0 80px;
  align-self: center;
}

.container {
  position: relative;
  color: white;
  display: inline;
  top: 73px;
  left: 8%;
}

.container img {
  width: 8%;
}

.paresfaltantes {
  position: absolute;
  top: -44px;
  left: 86px;
  font-size: 30px;
}

@media (max-width: 748px) {
  .logoBlanco {
    width: 100px;
    grid-column-end: span 2;
  }

  .regresar {
    width: 100%;
    grid-column-end: span 2;
    margin: 0 0px;
    padding: 0;
  }

  .container {
    top: 106px;
    left: 0%;
  }

  .container img {
    width: 130px;
  }

  .game {
    margin-top: -20px;
  }
}
